@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,700;0,900;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Raleway', sans-serif;
}

body {
  background: #209cee;
  padding: 30px;
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-right: 100px;
  -webkit-align-items: center;
          align-items: center;
}

.lg-container {
  width: 250px;
  margin: auto;
}

img {
  margin-bottom: 30px;
  fill: white !important;
  -webkit-filter: invert(1);
          filter: invert(1);
}


.task-list {
  background: #e8e8e8;
  border-radius: 4px;
  width: 450px;
  padding: 5px;
}

input {
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  display: -webkit-flex;
  display: flex;
  font-size: 15px;
  padding: 10px 10px;
}

.task {
  -webkit-align-items: center;
          align-items: center;
  background: #fff;
  height: 50px;
  border-radius: 3px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  display: -webkit-flex;
  display: flex;
  font-size: 12px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 6px;
  padding: 3px 10px;
}

button {
  font-weight: bold;
}

.btn-done {
  margin-right: 5px;
  background-color:#44c767;
	border-radius:5px;
	border:1px solid #18ab29;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-size:17px;
	padding: 5px 10px;
	text-decoration:none;
}

.btn-done:hover {
	background-color:#5cbf2a;
}
.btn-done:active {
	position:relative;
	/* top:1px; */
}

.btn-del {
  margin-right: 5px;
  background-color:#f24437;
	border-radius:5px;
	border:1px solid #d02718;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-size:17px;
	padding: 5px 10px;
	text-decoration:none;
}

.btn-del:hover {
	background-color:#c62d1f;
}
.btn-del:active {
	position:relative;
	/* top:1px; */
}
